import React, { useContext, useState } from "react";
import '../Cart.css'
import './Order.css'
import { Helmet } from 'react-helmet';
import { useNavigate } from "react-router";
import { FadeIn } from "react-slide-fade-in";
import { PaymentMethod } from "./PaymentMethod";
import { ProceedOrder } from "../../../api/endpoints/order";
import toastrService from "../../../services/toastr.service";
import translationService from "../../../services/translation.service";
import { ReactComponent as BackIcon } from '../../../images/leftIcon.svg'
import { CartContext } from "../../../context/cart-context";
import { WindowContext } from "../../../context/window-context";
import { UserContext } from "../../../context/user-context";
import { getRoute } from "../../../services/routes.service";
import { Ad } from "../Ad";
import useWindowSize from "../../../hooks/UseWindowSize";

const ShippingData = () => {
  const { 
    userData, 
    setUserData, 
    handleImageClick, 
    handleInputChange, 
    handlePaymentMethodChange, 
    onCalcSymbolsOnlyChange, 
    isActive,
    setIsActive 
  } = useContext(UserContext);
  const { cart, endedPrice } = useContext(CartContext);
  const { paymentWindow, setPaymentWindow} = useContext(WindowContext);
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);
  const [buttonText, setButtonText] = useState("Вибрати..");
  const [loading, setLoading] = useState(false);

  var navigate = useNavigate();
  var size = useWindowSize();
  const isMobile = size.width < 620

  const handleOrderConfirmation = async () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false); 
    }, 5000);
    if (!userData.town 
      || !userData.postOffice 
      || !userData.paymentMethod
      || !userData.name
      || !userData.phone
      || !userData.email) {
      setUserData((prevOrder) => ({
        ...prevOrder,
        selectedImage: prevOrder.selectedImage || null,
        town: prevOrder.town || "",
        postOffice: prevOrder.postOffice || "",
        paymentMethod: prevOrder.paymentMethod || "",
        name: prevOrder.name || "",
        phone: prevOrder.phone || "",
        email: prevOrder.email || "",
      }));

      setIsFormSubmitted(true);
      return;
    }

    try {
      await ProceedOrder({
        name: userData.name,
        phone: userData.phone,
        email: userData.email,
	      selectedImage: userData.selectedImage,
	      town: userData.town,
	      postOffice: userData.postOffice,
	      paymentMethod: userData.paymentMethod,
        products: cart.data,
        summary: endedPrice
      });

      toastrService.callToastr("Order placed successfully!");
      navigate(getRoute(`order_confirmation`));
    } catch (error) {
      setLoading(false);
      toastrService.callToastr("Error placing the order. Please try again");
    }
  };

  const handleButtonChange = (text: string) => {
    setButtonText(text);
  };

  const handlePayment = () => {
    setPaymentWindow(!paymentWindow);
    setIsActive(!isActive);
  };

  const postIcons = [
    {
      name: "Нова Пошта",
      src: "https://play-lh.googleusercontent.com/mtyOm0Rp0PeG_BWE7M5j9gBWuU1Du34LLj-dLdSE1-006_BkFg32W3Cca00l2BBvNM0",
      alt: "Nova Post",
    },
    {
      name: "Укр Пошта",
      src: "https://www.ukrposhta.ua/uploads/files/shares/pin_2.png",
      alt: "Ukr Post",
    },
    {
      name: "Meest",
      src: "https://www.ukraqua.com/wp-content/uploads/2022/04/group_303_0.png",
      alt: "Meest",
    },
  ];

  return (
    <div className="grid md:grid-cols-2 grid-cols-1 mt-[50px]">
      <FadeIn
        from="bottom"
        positionOffset={700}
        triggerOffset={200}
        delayInMilliseconds={-550}
        durationInMilliseconds={850}
      >
          <Helmet>
            <title>Введіть ваші дані для доставки | TVOI</title>
          </Helmet>
          <div className="p-3">
            <div className="w-full flex justify-center">
              <div className="step-style text-secondary flex items-center cursor-pointer" onClick={() => navigate(-1)}>
                <BackIcon className="w-[30px] pr-2"/>
                <div>Крок 2/2</div>
              </div>
            </div>
            
              <div className="order-element">
                <label className="inputStyle">&nbsp;&nbsp;&nbsp;&nbsp;Виберіть зручну для вас Пошту:*</label>
                <div className="rowStyle justify-content-center p-3">
                  {postIcons.map((icon) => (
                    <div
                      key={icon.name}
                      className={`post-icon-container ${
                        userData.selectedImage === icon.name ? "selected" : ""
                      }${isFormSubmitted && !userData.selectedImage ? "error" : ""}`}
                      onClick={() => handleImageClick(icon.name)}
                    >
                        <img className="post-icon-size" src={icon.src} alt={icon.alt} />
                        <span>{icon.name}</span>
                    </div>
                  ))}
                </div>

                <label className="inputStyle">&nbsp;&nbsp;&nbsp;&nbsp;Введіть місто та номер відділення:*</label>
                <div className="rowStyle w-100">
                  <div className="columnStyle m-2 component-fill">
                    <input
                      type="text"
                      name="town"
                      className={`form-control ${isFormSubmitted && !userData.town ? "error" : ""}`}
                      placeholder="Напр.: Львів"
                      value={userData.town}
                      onChange={handleInputChange}
                    />
                  </div>

                  <div className="columnStyle m-2 component-fill">
                  <input
                      type="text"
                      name="postOffice"
                      className={`form-control ${isFormSubmitted && !userData.postOffice ? "error" : ""}`}
                      placeholder="Напр.: відл. 34"
                      value={userData.postOffice}
                      onKeyPress={onCalcSymbolsOnlyChange}
                      onChange={handleInputChange}
                    />
                  </div>
                </div>

              </div>
              <div className="order-element">
                <div className="inputStyle columnStyle mt-2 ">&nbsp;&nbsp;&nbsp;&nbsp;
                  Виберіть спосіб оплати:*
                </div>
                <button
                  className={`showbutton border ${isActive ? 'active' : ''} ${isFormSubmitted && !userData.paymentMethod ? 'error' : ''}`}
                  onClick={handlePayment}
                >
                  {buttonText}
                  <span className="arrow"></span>
                </button>

                <span className="inputStyle">
                  Маєте запитання? 
                  <a href="https://www.instagram.com/love.tvoi/?igshid=YmMyMTA2M2Y%3D"> Напишіть нам сюди</a>
                </span>
              </div>

            </div>

            <div className="confirm-footer">
              <div className="w-100">
                <button className="btnConfirmCartStyle success-action" onClick={handleOrderConfirmation} disabled={loading}>
                  {loading ? 'Завантаження...' : translationService.translate('ok|A')}
                </button>
              </div>
            </div>
          {paymentWindow && <PaymentMethod 
          onCancel={handlePayment} 
          handleButtonChange={handleButtonChange} 
          handlePaymentMethodChange={handlePaymentMethodChange}/>}
      </FadeIn>
      {!isMobile ? <div className='ad-pos'>
        <Ad title='Ііі... останній крок і твоє замовлення уже відправляється до тебе. Не зупиняйся!' image='animals/cat_4.png'/>
      </div> : <></>}
    </div>
  );
};

export default ShippingData;